<template>
    <v-navigation-drawer
      v-model="drawerRight"
      fixed
      right
      clipped
      app
    >
      <div class="personal-avatar-box" >
				<div class="personal-avatar-mask" :style="{backgroundImage: 'url(' + userinfo_avatar + ')' }"></div>
        <v-avatar
          class="personal-avatar"
          :tile=false
          :size="100"
        >
          <img :src="userinfo_avatar" alt="avatar">
        </v-avatar>
        <p class="personal-username headline">{{ userinfo_name }}</p>
      </div>
      <v-list two-line>
          <v-list-tile @click="">
            <v-list-tile-action>
              <v-icon color="blue-grey">face</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{ userinfo_userid }}</v-list-tile-title>
              <v-list-tile-sub-title>userid</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>

          <v-divider inset></v-divider>

          <v-list-tile @click="">
            <v-list-tile-action>
              <v-icon color="blue-grey">work</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{ userinfo_position }}</v-list-tile-title>
              <v-list-tile-sub-title>position</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>

          <v-divider inset></v-divider>

          <v-list-tile @click="">
            <v-list-tile-action>
              <v-icon color="blue-grey">mail</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{ userinfo_email }}</v-list-tile-title>
              <v-list-tile-sub-title>email</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>

          <v-list-tile @click="">
            <v-list-tile-action>
              <v-icon color="blue-grey">phone_iphone</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{ userinfo_mobile }}</v-list-tile-title>
              <v-list-tile-sub-title>mobile</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
          
        </v-list>

    </v-navigation-drawer>
</template>

<script>
export default {
	props: ['drawerRight'],
    data() {
        return {
            // userinfo
            userinfo_name: '',
            userinfo_avatar: require('../assets/av.png') ,
            userinfo_userid: '',
            userinfo_position: '',
            userinfo_email: '',
            userinfo_mobile: '',
        }
    },
    created() {
        this.$api.getUserInfo({
            code: this.getQueryVariable('code')
        }).then(v => {
            alert(JSON.stringify(v))

            this.userinfo_name = v.data.name;
            this.userinfo_avatar = v.data.avatar;
            this.userinfo_userid = v.data.userid;
            this.userinfo_position = v.data.position;
            this.userinfo_email = v.data.email;
            this.userinfo_mobile = v.data.mobile;

            this.$store.commit('SET_USERINFO', {
                userinfo_avatar: v.data.avatar,
                userinfo_userid: v.data.userid
            })
            
        }).catch(e => {
            console.log(e)
        })
    },
    methods: {
        //获取url中的参数
        getQueryVariable(variable) {
            let query = window.location.search.substring(1);
            let vars = query.split("&");
            for (let i=0, len = vars.length; i < len; i++) {
                    let pair = vars[i].split("=");
                    if(pair[0] == variable){return pair[1];}
            }
            return(false);
        }
    }
}
</script>

<style lang="scss" scoped>
.personal-avatar-box {
  width: 100%;
  height: 200px;
  position: relative;
  z-index: 1;
  overflow: hidden;

  .personal-avatar-mask {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
		background-size: 50px;
		background-position: -10px;
		background-repeat: repeat;
    filter: blur(7px);
    margin: -9px;
  }

  .personal-avatar {
    position: absolute;
    z-index: 3;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
  }
  .personal-username {
    position: absolute;
    z-index: 3;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
  }
}
</style>
