import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    USERINFO_USERID: '',
    USERINFO_AVATAR: require('./assets/av.png'),
  },
  mutations: {
    // 保存userinfo
    SET_USERINFO: (state, params) => {
      state.USERINFO_AVATAR = params.userinfo_avatar;
      state.USERINFO_USERID = params.userinfo_userid;
    }
  },
  actions: {

  }
})
