import { get, post } from "./axios"

const api =  {
    getUserInfo(params) {
        return get('/getuserinfo', params);
    }
}

// api
export default {
  install: function (Vue, Option) {
    Object.defineProperty(Vue.prototype, '$api', { value: api })
  }
}